.about-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 5rem 0;
}

.about-container {
  max-width: 56rem;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.about-title {
  font-size: 2.25rem;
  line-height: 2.5rem;
  font-family: serif;
  font-weight: 700;
  color: white;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .about-title {
    font-size: 3rem;
    line-height: 1;
  }
}

.about-content {
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  padding: 2rem;
}

@media (min-width: 768px) {
  .about-content {
    padding: 3rem;
  }
}

.about-text {
  font-size: 1.125rem;
  line-height: 1.75rem;
  color: rgb(209 213 219);
  line-height: 1.75;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .about-text {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
}

.about-button {
  background-color: transparent;
  border: 2px solid rgb(180, 143, 86);
  color: rgb(180, 143, 86);
  font-weight: 600;
  padding: 0.75rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  line-height: 1.75rem;
  transition: all 0.3s;
  transform: scale(1);
}

.about-button:hover {
  background-color: rgb(180, 143, 86);
  color: white;
  transform: scale(1.05);
}
